html,body {
  height: 100%;
  width: 100%;
}
.container {
  position: absolute;
  top: 0px;
}
.custom-modal {
  position: absolute;
  background: black;
  color: white;
  padding: 20px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.close-btn {
  float: right;
  font-size: 20px;
  color: #4100a9;
  font-weight: 700;
  margin: 15px;
}